.form .comments {
  border-top: 2px solid $background-button;
}

.comments {
  margin-top: 24px;
  padding: 0;

  @media (min-width: $tablet-width) {
    margin-top: 65px;
  }
}

.comments__title {
  font-size: 24px;
  line-height: 26px;
  width: 45%;
  margin-bottom: 37px;

  @media (min-width: $tablet-width) {
    font-size: 36px;
    line-height: 36px;
    width: auto;
    padding-right: 42px;
    padding-bottom: 0;
    margin-bottom: 76px;
  }
}

.comments__input {
  font-family: $font-family;
  font-size: 16px;
  line-height: 18px;
  color: $text-color;
  text-transform: uppercase;
  padding-top: 9px;
  padding-bottom: 125px;
  min-height: 150px;
  width: 100%;
  border: 1px solid $border-menu;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 30px;
    padding-top: 10px;
    padding-bottom: 134px;
    padding-left: 19px;
    min-height: 174px;
  }
}
